import React, { Fragment } from 'react';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Circle from 'components/Circle';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'

import { options } from '../Steps/Step1'

const Dot = ({ duration, pain, num, forceDesktop }) => {
  return (
    <Flex width={forceDesktop ? 1/3 : mobileOrDesktop(1, 1 / 3)} alignItems="center" mt="1em" px="1em">
      <Box width="2.5em" mr="0.5em">
        <Circle bg="orange" fontWeight="bold" color="white" fontSize="1.75em">{num}</Circle>
      </Box>
      <Box flex={1} borderLeft="2px solid" borderColor="lightBgPurple" pl="1em">
        {options[duration] && (
          <Fragment>
            <Text.inline color="titleBlue" mr="1em" fontWeight="bold">持續天數</Text.inline>
            <Text.inline mr="1em" fontSize="1.25em">{options[duration].label}</Text.inline>
          </Fragment>
        )}
        <Text color="titleBlue" mt="0.75em" fontWeight="bold">症狀描述</Text>
        <Text fontSize="1.25em">{pain || '無'}</Text>
      </Box>
    </Flex>
  );
};

export default Dot;
