import React, { createElement, Fragment, useState } from 'react';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Link from 'components/Link';
import Text from 'components/Text';
import Yes from 'components/Yes';
import No from 'components/No';
import Image from 'components/Image';
import Button from 'components/Button';
import Circle from 'components/Circle';
import Modal from 'components/Modal';
import { mobileOrDesktop } from 'components/ThemeProvider/theme'
import withResponsive from 'hoc/withResponsive'

import Downloader from '../../Downloader'

import Title from '../Title'
import Desktop from './Desktop';
import Mobile from './Mobile';

import { questions, nameId } from '../Steps/Step2'

import no from './no.png'
import yes from './yes.png'

const MemoResult = ({
  memo,
  onEdit,
  onConfirm,
  isMobile,
  withDownload,
  forceDesktop,
  isDownload,
}) => {
  const { dots, answers, yesNo } = memo
  const [dlIsOpen, setDlOpen] = useState()
  return (
    <div>
      <Title mt="2em" title="我的看診備忘錄" />
      <Box
        my="2em"
        mx={forceDesktop ? '5%' : mobileOrDesktop('10%', '5%')}
      >
        {createElement(isMobile && !forceDesktop ? Mobile : Desktop, { dots, forceDesktop, isDownload })}
      </Box>
      <Box mx={forceDesktop ? '5%' : mobileOrDesktop('10%', '5%')}>
        <Flex mt="1em" flexWrap="wrap" borderTop="2px solid" borderBottom="2px solid" borderColor="lightBgPurple" py="2em">
          {questions.map((q, i) => (
            <Box
              opacity={typeof yesNo[nameId[i]] !== 'boolean' && '0.5'}
              width={forceDesktop ? 1/3 : mobileOrDesktop(1, 1/3)}
              borderLeft="2px solid"
              borderColor="lightBgPurple"
              px="1em"
              position="relative"
              key={i}
            >
              <Box fontWeight="bold">{q}</Box>
              <Flex alignItems="center" pt="0.5em" pb={questions.length === i + 1 ? 0 : '1.25em'}>
              {typeof yesNo[nameId[i]] === 'boolean' ? (
                <Box width="1.5em" color="orange" mr="1em" textAlign="left">
                  {yesNo[nameId[i]] ? (isDownload ? <Image src={yes} /> : <Yes width="100%" />) : (isDownload ? <Image src={no} /> : <No width="100%" />)}
                </Box>
              ) : <Text fontSize="1.25em">未填寫</Text>}
              {answers[nameId[i]] && <Text fontSize="1.25em">{answers[nameId[i]]}</Text>}
                <Box position="absolute" left="0" top="0" transform="translateX(-55%)">
                  <Circle width="1em" bg="lightBgPurple" />
                </Box>
              </Flex>
            </Box>
          ))}
        </Flex>
        {!isDownload && (
          <Fragment>
          <Flex justifyContent="center" my="2em">
            {onEdit && <Button mx="0.5em" px={forceDesktop ? '3.5em' : mobileOrDesktop('2.5em', '3.5em')} onClick={onEdit}>修改</Button>}
            {onConfirm && <Button mx="0.5em" px={forceDesktop ? '3.5em' : mobileOrDesktop('2.5em', '3.5em')} onClick={onConfirm}>確認</Button>}
            {withDownload && (
              <Fragment>
                <Button mx="0.5em" px={forceDesktop ? '3.5em' : mobileOrDesktop('2.5em', '3.5em')} onClick={() => setDlOpen(true)}>下載</Button>
                <Modal isOpen={dlIsOpen} onRequestClose={() => setDlOpen(false)}>
                  <Text.h3 textAlign="center">下載備忘錄</Text.h3>
                  <Downloader memo={memo} isMobile />
                </Modal>
                {!isMobile && <Button mx="0.5em" to="/memo" is={Link} px={forceDesktop ? '3.5em' : mobileOrDesktop('2.5em', '3.5em')}>回看診備忘錄</Button>}
              </Fragment>
            )}
          </Flex>
          {withDownload && isMobile && (
            <Box textAlign="center">
              <Button mx="0.5em" to="/memo" is={Link} px={forceDesktop ? '3.5em' : mobileOrDesktop('2.5em', '3.5em')}>回看診備忘錄</Button>
            </Box>
          )}
          </Fragment>
        )}
      </Box>
    </div>
  );
};

export default withResponsive(MemoResult);
