import React, { Fragment } from 'react';

import Link from 'components/Link';
import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Button from 'components/Button';
import StyledImage from 'components/Image';
import Title from 'components/Title';
import Container from 'components/Container';
import { mobileOrDesktop } from 'components/ThemeProvider/theme';
import withResponsive from 'hoc/withResponsive'

import note from './note.svg';
import remind from './remind.svg';
import bubble from './bubble.svg';
import Layout from '../Layout';
import Downloader from '../Downloader';
import notice from '../TestPage/notice.svg';
import Sharer from './Sharer'

export const askings = [
  '依據目前的症狀，我可能罹患什麼病？',
  '什麼時候用藥，用藥時需要注意的事項？',
  '我需要進行哪些治療來緩解我的症狀？',
  '藥物可能有哪些副作用？',
  '如果想更了解症狀，可以搜尋哪些資料？',
  '若症狀緩解，是否可以提早停藥？',
  '我可以怎麼預防再發生同樣的症狀？'
]

const Notice = ({ memo, isMobile }) => {
  const noticeList = (
    <Fragment>
      <Title icon={remind} fontSize="1.5em" title="預習看診時可以詢問醫師的問題" description="見到醫師，照著問就會沒問題！" justifyContent="center" pt="1.5em" />
      <Flex flexWrap="wrap">
        {askings.map((a, i) => (
          <Box width={mobileOrDesktop(1, 1/2)} key={i}>
            <Flex
              alignItems="center"
              py="1em"
              mx="2em"
              borderBottom="1.5px solid"
              borderColor="titleBgOrange"
              key={i}
            >
              <StyledImage width="2.5em" src={bubble} />
              <Box width={1} mx="1em" fontWeight="bold" fontSize={mobileOrDesktop('1em', '1.25em')}>{a}</Box>
            </Flex>
          </Box>
        ))}
      </Flex>
      <Text textAlign={mobileOrDesktop('left', 'center')} pt="1em" pb="2em" mx="2em" fontSize={mobileOrDesktop('0.75em', '1em')}>✻想進一步了解藥物使用的方法、存放條件等注意事項，可以在領藥時進一步諮詢你的藥師！</Text>
    </Fragment>
  )
  return (
    <Layout headerBg={mobileOrDesktop('lightBgOrange', 'auto')}>
      <Container py="2em">
        {memo && (
          <Box px={mobileOrDesktop(0, '4em')} bg="titleBgOrange" py="1.5em" borderRadius="1em" mx={mobileOrDesktop('1em', '5em')}>
            <Title
              imgwidth="5em"
              flexWrap="wrap"
              justifyContent="center"
              flexDirection="column"
              width={1}
              icon={note}
              textAlign="center"
              title="恭喜你完成看診備忘錄"
              description={'立即下載備忘錄，讓他成為你看診時的好幫手'}
            />
            <Downloader memo={memo} noticeList={noticeList} />
          </Box>
        )}
        {noticeList}
        {!memo && <Box px={mobileOrDesktop('3em', '4em')} mx={mobileOrDesktop('1em', 0)} bg="titleBgOrange" py="2em" borderRadius="1em" mt="3em">
          <Text textAlign="center" color="titleBlue" fontWeight="bold">
            看醫生時{memo ? '同時下載「看診備忘錄」及「參考問題」' : '可以下載「參考問題」'}，提醒自己該跟醫師確認的事項
            <Downloader memo={memo} noticeList={noticeList} mx={mobileOrDesktop(0, '15%')} />
          </Text>
        </Box>}
        {memo && (
          <Title
            flexWrap="wrap"
            mobileWidth={13/50}
            icon={notice}
            textAlign={mobileOrDesktop('center', 'left')}
            fontSize="1.25em"
            mobileFontSize="1.1em"
            fontWeight="normal"
            title="看診後，別忘了再回來紀錄醫生的回答"
            description="幫助你未來有效追蹤自己的病況！"
            justifyContent="center"
            pt="1.5em"
            borderTop="1px solid"
            borderColor="lightBgPurple"
            mx="2em"
            mt="1em"
          />
        )}
        {memo ? (
          <Flex flexDirection={mobileOrDesktop('column', 'row')} my="1em" justifyContent="center" alignItems="center">
            <Sharer mb={mobileOrDesktop('1em', 0)} mr={mobileOrDesktop(0, '1em')}>分享看診好幫手</Sharer>
            <Box><Button is={Link} to="/memo/history">我的看診備忘綠</Button></Box>
          </Flex>
        ) : (
          <Box textAlign="center" my="2em">
            <Button is={Link} to="/memo/new/1">製作看診備忘綠</Button>
          </Box>
        )}
      </Container>
    </Layout>
  );
}

export default withResponsive(Notice)
