import React, { Fragment, useState } from 'react';
import Measure from 'react-measure'

import Box from 'components/Box';
import Flex from 'components/Flex';

import Dot from './Dot'
import BodyPoints from '../BodyPoints'

const Desktop = ({ dots, forceDesktop, isDownload }) => {
  const [dimensions, setDimension] = useState({})
  const renderDot = (dot, i) => <Dot key={i} num={i + 1} forceDesktop={forceDesktop} {...dot} />
  return (
    <Fragment>
      <Flex
        position="relative"
        height={dimensions.height}
        alignItems="center"
        flexWrap="wrap"
        mx="-1em"
      >
        {dots.slice(0, 4).reduce((all, dot, i) => all.concat([
          renderDot(dot, i),
          i % 2 ? null : <Box key={`space-${i}`} width={1 / 3} px="1em" />,
        ]), [])}
        <Measure bounds onResize={({ bounds }) => setDimension(bounds)}>
          {({ measureRef }) => (
            <Box
              position="absolute"
              top="0"
              left="38%"
              right="38%"
              bg="bgOrange"
              p="1em"
              borderRadius="1em"
              ref={measureRef}
            >
              <BodyPoints dots={dots} isDownload={isDownload} />
            </Box>
          )}
        </Measure>
      </Flex>
      <Flex
        alignItems="center"
        flexWrap="wrap"
        mt="1em"
        mx="-1em"
      >
        {dots.slice(4).map((dot, i) => renderDot(dot, i + 4))}
      </Flex>
    </Fragment>
  );
};

export default Desktop;
