import React, { Fragment } from 'react';

import Box from 'components/Box';

import Dot from './Dot'
import BodyPoints from '../BodyPoints'

const Desktop = ({ dots, isDownload }) => {
  return (
    <Fragment>
      <Box
        bg="bgOrange"
        p="1em"
        borderRadius="1em"
      >
        <BodyPoints dots={dots} isDownload={isDownload} />
      </Box>
      {dots.map((dot, i) => <Dot key={i} num={i + 1} {...dot} />)}
    </Fragment>
  );
};

export default Desktop;
